.App {
  text-align: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  gap: 0rem;

}

.intro {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0rem;
}

.App-header {
  
  display: flex;
  justify-content: center;  /* ðŸ‘ˆ THIS centers everything horizontally */
  align-items: center;
  gap: 0rem;
}

.intro h1,
.intro p {
  margin: 0.3rem 0; /* smaller top & bottom margins */
  line-height: 1.6; /* tighter line spacing */
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f4e4fff2;
  min-height: 100vh;
  display: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1.5vmin);
  color: rgb(0, 0, 0);
}

.App-link {
  color: #61dafb;
}

.LinkedIn-link {
  color: #61dafb;
}

.projects_header {
  width: 100%;     /* take full width */
  text-align: center;
  padding-top: 100px;
  font-size: calc(20px + 2vmin);
}

.SkillSwap-section {
  display: flex;              /* horizontal layout */
  justify-content: center;    /* center the whole section horizontally */
  align-items: flex-start;    /* keep items aligned at top */
  gap: 80px;                  /* space between text and image */
  padding-top: 100px;
  max-width: 1000px;          /* optional: control width */
  margin: 0 auto;             /* center container on page */
}

.SkillSwap-intro {
  text-align: left;           /* keep text left-aligned */
}

.SkillSwap-image {
  width: 300px;
  height: auto;
  border-radius: 0px;
  object-fit: cover;
}



.LoanProject-section {
  display: flex;              /* horizontal layout */
  justify-content: center;    /* center the whole section horizontally */
  align-items: flex-start;    /* keep items aligned at top */
  gap: 80px;                  /* space between text and image */
  padding-top: 100px;
  max-width: 1000px;          /* optional: control width */
  margin: 0 auto;        /* smaller padding, not huge offsets */
}

.LoanProject-intro {
  text-align: left;            /* left-align text */
}

.LoanProject-image {
  width: 300px;
  height: auto;
  border-radius: 0px;
  object-fit: cover;
}



.PawFit-section {
  display: flex;              /* horizontal layout */
  justify-content: center;    /* center the whole section horizontally */
  align-items: flex-start;    /* keep items aligned at top */
  gap: 80px;                  /* space between text and image */
  padding-top: 100px;
  max-width: 1000px;          /* optional: control width */
  margin: 0 auto;   

}

.PawFit-intro {
  text-align: left;            /* left-align text */
}

.PawFit-image {
  width: 300px;
  height: auto;
  border-radius: 0px;
  object-fit: cover;
}

.project-link {
  color: inherit;        /* uses the same color as your text */
  transition: color 0.3s ease;
}

.project-link:hover {
  color: #b361fb; /* or any highlight color */
}

.inline-image-nasa {
  width: 60px;        /* adjust size */
  height: 60px;
  vertical-align: middle; /* aligns it nicely with the text */
  margin: 0 px;      /* space before and after */
}

.inline-image-palantir {
  width: 48px;        /* adjust size */
  height: 48px;
  vertical-align: middle; /* aligns it nicely with the text */
  margin: 0 px;      /* space before and after */
}